//        - main-header,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-header {

  $block-name:                &; // #{$block-name}__element

  @media (max-width: ($menu-desktop-width - 1)) {
    .header-nav {
      @include container;
      &__logo {
        margin: auto;
      }
    }
    .header-top,
    .menu.menu--main {
      display: none;
    }

  }

}
