.team {

  $block-name:                &; // #{$block-name}__element

  &__cards {
    @include row;
  }

  .t-card {
    @include make-col(3);
    margin-bottom: 25px;
    @media (max-width: ($menu-desktop-width - 1)) {
      @include make-col(12);
    }
  }

}
